//модальное окно
.modal__content {
  display: none;


}
.modal__open {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 9, 3, 0.2);
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  z-index: 1;
}
.modal_close {
  position: absolute;
  right: 10px;
  top: 10px;
  &:after {
    content: "X";
    font-size: 15px;
    color: black;
    width: 30px;
    height: 30px;
    background-color: bisque;
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.form {
  position: relative;
  background-color: white;
  border: 5px solid #fba603;
  border-radius: 20px;
  padding: 50px 50px 0px 50px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  max-width: 500px;
  width: 80%;
  margin: 10px;
  text-align: center;
}
input {
  overflow: visible;
  border: 2px solid #fba603;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 5px 32px;
}
.btn {
  -webkit-appearance: button;
  background-image: -webkit-linear-gradient(0deg, #ff9800 0%, #ffc107 100%);
  background-image: -ms-linear-gradient(0deg, #ff9800 0%, #ffc107 100%);
  border-radius: 50px;
  padding: 20px 53px;
  border: none;
  margin-bottom: 30px;
  color: white;
}
.confidence {
  font-size: 12px;
}


